@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
/* base css file  */

/* Roboto font */

/* Roboto Condensed font */


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
}

button {
    border: none;
}

:root{
    /* font sizes */
    --size-small: 14px;
    --size-normal: 16px;
    --size-mid:18px;
    --size-mid2:21px;
    --size-big: 24px;
    --size-extreme: 28px;
    /* font weight */
    --weight-small: 300;
    --weight-normal: 400;
    --weight-mid: 500;
    --weight-big: 700;
    /* typefaces */
    --roboto-normal: 'Roboto', sans-serif;
    --roboto-bold: 'Roboto Condensed', sans-serif;
    /* font color */
    --font-lighter: rgb(218, 218, 218);
    --font-light: rgb(136, 136, 136);
    --font-mid: rgb(83, 83, 83);
    /* theme colors */
    --color-primary: rgba(206, 43, 43, 0.733);
    --color-primary-mid: rgb(170, 12, 12);
    --color-primary-dark: rgba(116, 17, 17, 0.733);
    --color-primary-darker: rgba(26, 0, 0, 0.973);
    /* background */
    --bg-main: rgb(241, 241, 241);
    --bg-dark: rgb(236, 236, 236);

    /*padding*/
    --padding-hori-mobile: 10px;
    --padding-vert-mobile:12px;
    --padding-vert-mobile-big:15px;
}

html{
    height: 100%;
}

body{
    font-family: 'Roboto', sans-serif;
    font-family: var(--roboto-normal);
    height: 100%;
}

header{

}

main{
    /* every page */
    height: 100%;
    width: 100%;
    background: linear-gradient(to bottom,rgba(0, 0, 0, 0.747),rgba(0, 0, 0, 0.301)),url(/komix/static/media/background.1920d37a.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.loader-container{
    width: 100%;
}

.loader{
    min-width: 120px;
    border-radius: 2px;
    height: 30px;
    background-color: rgb(236, 236, 236);
    background-color: var(--bg-dark);
    position: relative;
    overflow: visible;
}
.loader::after{
    position: absolute;
    left: -100%;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.719), transparent);
    display: inline-block;
    -webkit-animation: shine 2s infinite;
            animation: shine 2s infinite;
    content: '';
}
@-webkit-keyframes shine {
    100% {
      -webkit-transform: translateX(300%);
              transform: translateX(300%);
    }
  }
@keyframes shine {
    100% {
      -webkit-transform: translateX(300%);
              transform: translateX(300%);
    }
  }

.loader:not(:first-child){
    margin-top: 10px;
}
.app-container{
    position: relative;
    overflow: hidden;
    min-height: 100vh;
}

.app-container > div {
    padding-top: 65px;
}
.app-header{
    position: fixed; /*because the side menu will be position:absolute*/
    height: 65px;
    width: 100%;
    display: flex;
    padding: 4px 3%;
    justify-content: space-between;
    align-items: center;
    padding-top: 5px;
    overflow: hidden;
    transition: all 0.1s linear;
}

.app-header-child{
    /* every direct child of .app-header */
    height: 60%;
}

.app-header-logo > * {
    /* Komix logo img */
    height: 100%;
}

.app-header-hamburger {
    display: flex;
    align-items: center;
    justify-content: center;
}
.menu_button-container{
    /* will be assigned height from the parent */
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: currentColor;
    background: transparent;
} 

.menu_button-container > svg{
    fill: currentColor;
    /* svg will be filled with the current color of the parent component */
    height: 35px;
}
.app-homepage{
    display: flex;
    min-height: 100vh;
    justify-content: center;
}

.home_searchbox-container{
    box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.674);
    font-size: var(--size-normal);
    margin-top: 30%;
    width: 90%;
    height: 55px;
    position: relative;
}

.side_menu{
    position: fixed;
    top: 0;
    z-index: 5;
    background: white;
    width: 70vw;
    height: 100vh;
    display: flex;
    z-index: 10;
    justify-content: space-between;
    flex-direction: column;
    transition: all 0.2s linear;
}
.side_menu-open{
    /* when side menu is open */
    right: 0;
}
.side_menu-closed{
    /* when side menu is closed */
    right: -100%;
}

.side_menu-button{
    /* container for MenuButton*/
    height: 74px;
    width: 50px;
}

.side_menu-link-container{
    /* container for side_menu-link(s) */
    display: flex;
    width: 100%;
    flex-direction: column;
}

.side_menu-link{
    width: 100%;
    min-height: 60px;
    display: flex;
    flex-direction: column;
    color: var(--font-mid);
    font-size: var(--size-mid);
    transition: background 0.15s linear;
    display: flex;
    flex-direction: column;
}
.side_menu-link:not(:last-child){
    margin-bottom: 15px;
}

.side_menu-link-child-container{
    display: flex;
    width: 100%;
    height: 60px;
    padding: 15px 0;
    cursor: pointer;
    transition: background 0.2s linear;
}
.side_menu-link-child-container:active{
    background: rgba(5, 111, 197, 0.082);
}
.side_menu-link-child{
    /* direct child of .side_menu_link */
    padding: 0 3%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}
.side_menu-link-child > img{
    /* all icons inside side menu link */
    height: 100%;
    padding: 3px;
}
.side_menu-link-label{
    margin-left: 1px;
    /* the side menu link that is the label */
    margin-right: auto;
}

/* *8****************************************************************** */

.side_menu-link-submenu{
    padding: 0 6%;  
    list-style-type: none;
    font-size: var(--size-normal); 
}
.side_menu-link-submenu-hidden{
    display: none;
}


/* ******************************************************************* */

.side_menu-footer{
    /*container for side_menu-footer */
    padding: 20px 0;
    text-align: center;
    color: rgb(87, 87, 87);
}
