.comic_page{
    background-color: var(--bg-main);
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* padding: 0 15px; */

/* Comic_page-content */
.comic_page-content{
    position: relative;
}
.comic_page-content-poster{
    position: relative;
    height: 300px;
    width: 100%;
    overflow:hidden;
    background-position: left center;
    background-size: cover;
    background-repeat: no-repeat;
}
.comic_page-content-poster > div{
    background: rgba(59, 59, 59, 0.123);
    height: 100%;
    width: 100%;
}
.comic_page-content-poster-small{
    background-color: var(--bg-main);
    height: 220px;
    width: 142px;
    position: absolute;
    top: 140px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
    justify-content: center;
    overflow: hidden;
}
.comic_page-content-poster-small > img{
    height: 100%;
    border-radius: 2px;
}

/* Comic_page-information */
.comic_page-information{
 /*background: red; */
 flex: 1 1;  
}
.comic_page-information > * {
    /* padding: 0 var(--padding-hori-mobile); */
}

.comic_page-information-space{
    height: 60px;
    width: 100%;
    /*background-color: red; */
}
.comic_page-information-title{
    font-size: var(--size-extreme);
    font-family: var(--roboto-bold);
    color: var(--color-primary-darker);
    padding: 20px var(--padding-hori-mobile);
    border-bottom: 1px solid var(--font-lighter);
}
.comic_page-information-title > .loader{
    width: 350px;
}

.comic_page-information-details{
    display: flex;
    flex-direction: column;
    font-family: var(--roboto);
    font-weight: var(--weight-big);
}
.comic_page-information-details > * {
    font-size: var(--size-mid);
}

.comic_page-information-details-figures{
    display: flex;
    justify-content: space-between;
    /* background-color: green; */
    padding: var(--padding-vert-mobile) var(--padding-hori-mobile);
}

.comic_page-information-details-figures-pages{
    padding-left: var(--padding-hori-mobile);
}
.comic_page-information-details-figures-pages > .pages-key {
    color: var(--font-mid);
    letter-spacing: 2px;
}
.comic_page-information-details-figures-pages > .pages-value {
    font-family: var(--roboto-bold);
    color: var(--font-mid);
}


.comic_page-information-details-figures-price{
    padding-right: var(--padding-hori-mobile);
}
.comic_page-information-details-figures-price > .price-key {
    letter-spacing: 2px;
    color: var(--font-mid);
}
.comic_page-information-details-figures-price > .price-value{
    font-family: var(--roboto-bold);
    color: var(--font-mid);
}

.comic_page-information-details-link{
    padding: 0 var(--padding-hori-mobile);
}
.comic_page-information-details-link > a {
    text-decoration: none;
    display:block;
    width: 100%;
    height: 100%;
    font-size: var(--size-mid);
    color: var(--font-lighter);
    font-weight: var(--weight-big);
    font-family: var(--roboto-normal);
    padding: var(--padding-vert-mobile) 0;
    text-align: center;
    background-color: var(--color-primary-mid);
}
.comic_page-information-details-link > a:hover{
    cursor: pointer;
    text-decoration: underline;
}
.comic_page-information-details-link > a:active{
    background-color: var(--color-primary);
}

/* comic_page-information-description */
.comic_page-information-description{
    padding: var(--padding-vert-mobile-big) var(--padding-hori-mobile);
    font-size: var(--size-mid);
    letter-spacing: 1px;
}
.comic_page-information-description > .description {
    padding: var(--padding-vert-mobile-big);
}

/* creators */
.comic_page-information-creators{
    padding: 0 var(--padding-hori-mobile);
    border-top: 1px solid var(--font-lighter);
    background-color: var(--bg-dark);
    width: 100%;
}
.comic_page-information-creators-subheading{
    padding: var(--padding-vert-mobile) 0;
    font-size: var(--size-big);
    border-bottom: 1px solid var(--font-lighter);
    font-weight: var(--weight-big);
    color: var(--color-primary-darker);
}

.comic_page-information-creators-data{
    display: flex;
    flex-direction: column;
}
.comic_page-information-creators-data-creator{
    font-size: var(--size-mid);
    padding: var(--padding-vert-mobile) 0;
    font-weight: var(--weight-mid);
}
.comic_page-information-creators-data-creator:not(:last-child){
    border-bottom: 1px solid var(--font-lighter);
}

/* Share button */
.comic_page-sharebtn{
    background-color: var(--bg-dark);
    height: 43px;
    padding: 0 var(--padding-hori-mobile);
}

.comic_page-sharebtn > .share{
    cursor: pointer;
    font-family: var(--roboto);
    display: block;
    width: 100%;
    height: 100%;
    background-color: var(--color-primary-mid);
    font-size: var(--size-mid);
    font-weight: var(--weight-mid);
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}
.comic_page-sharebtn > .share:active{
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}

.comic_page-sharebtn > .share > .share-label {
    color: var(--font-lighter);
    font-weight: var(--weight-mid);
}
.comic_page-sharebtn > .share > .share-icon {
    fill: var(--font-lighter);
    margin-left: 10px;
}

/* the results page container */
.results_page{
    background-color: var(--bg-main);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.results_page > * {
    padding: 0 5px;
    box-shadow: 0 3px 10px 4px rgba(0, 0, 0, 0.075);
}

.results_page > .app-header{
    padding: 4px 3%;
}

/* results_page-searchbox */
.results_page-searchbox{
    position: relative;
    height: 50px;
    font-size: var(--size-normal);
    color:var(--font-mid);
}

/* the categories e.g comics, creators etc */
.results_page-categories{
    box-shadow: none;
    padding-top:20px;
    padding-bottom: 5px;
    display: flex;
    list-style-type: none;
    padding-left: 20px;
    border-bottom: 1px solid var(--font-lighter);
}
.results_page-category{
    color: var(--font-light);
}
.results_page-category > a{
    font-weight: var(--weight-mid);
    font-family: var(--roboto-bold);
    font-size: var(--size-mid);
    color: inherit;
    text-decoration: none;
}
.results_page-category:not(:last-child){
    margin-right: 15px;
}
.results_page-category-selected{
    color: var(--color-primary);
    border-bottom: 3px solid var(--color-primary);
}
.results_page-category-selected > a {
    font-weight: var(--weight-big);
}


/* results_page-content */
.results_page-content{
    box-shadow: none;
    padding: 0 15px;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    background: var(--bg-main);
}
.results_page-content-searched-term{
    margin-top: 5px;
    padding: 15px 10px;
    background: white;
    color: var(--font-mid);
    font-size: var(--size-small);
    box-shadow: 0 3px 10px 2px rgba(0, 0, 0, 0.075);
}
.results_page-content-searched-term > p > span {
    margin: 5px;
    color: var(--color-primary);
    font-weight: var(--weight-big);
}

body{
    margin: 0;
}

.results_page-content-data{
    margin-top: 20px;
    background-color: white;
    width: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    list-style-type: none;
}

.results_page-content-data-item{
    height: 110px;
    display: flex;
    padding: 5px;
}
.results_page-content-data-item:not(:last-child){
    margin-bottom: 5px;
}
.results_page-content-data-item:first-child{
    margin-top: 5px;
    display: flex;
}

.results_page-content-data-item-thumbnail{
    flex: 0 0 74px;
    background-color: var(--bg-main);
}
.results_page-content-data-item-thumbnail > img{
    height: 100%;
    width: 100%;
}

.results_page-content-data-item-text{
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: var(--font-mid);
}
.results_page-content-data-item-text > * {
    padding: 7px;
}
.results_page-content-data-item-text-title{
    font-size: var(--size-mid);
    font-weight: var(--weight-big);
    color: inherit;
    text-decoration: none;
}
.results_page-content-data-item-text:hover{
    color: var(--color-primary);
}

.results_page-content-data-item-price {
    color: var(--font-light);
}


.results_page-content-error{
    margin-top: 20px;
    flex: 1 1;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

:root{
    --loader-bg:rgb(223, 223, 223);
}

.results_page-content-loader{
    margin-top: 20px;
    background-color: white;
    width: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    list-style-type: none;
}

.results_page-content-loader-item{
    height: 110px;
    display: flex;
    padding: 5px;
}
.results_page-content-loader-item:not(:last-child){
    margin-bottom: 5px;
}
.results_page-content-loader-item:first-child{
    margin-top: 5px;
    display: flex;
}

.results_page-content-loader-item-thumbnail{
    flex: 0 0 87px;
    background-color: rgb(223, 223, 223);
    background-color: var(--loader-bg);
    position: relative;
    overflow-x: hidden;
}
.results_page-content-loader-item-thumbnail::after{
        width: 100%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        background-image: linear-gradient(to right,rgb(223, 223, 223),rgb(238, 238, 238),rgb(223, 223, 223));
        background-image: linear-gradient(to right,var(--loader-bg),rgb(238, 238, 238),var(--loader-bg));
        -webkit-animation: shimmer 1.3s infinite;
                animation: shimmer 1.3s infinite;
        content: '';
}


.results_page-content-loader-item-text{
    width: 100%;
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: var(--font-mid);
}
.results_page-content-loader-item-text > * {
    height: 28px;
    position: relative;
    overflow-x: hidden;
}
.results_page-content-loader-item-text > *::after{
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    background-image: linear-gradient(to right,rgb(223, 223, 223),rgb(238, 238, 238),rgb(223, 223, 223));
    background-image: linear-gradient(to right,var(--loader-bg),rgb(238, 238, 238),var(--loader-bg));
    -webkit-animation: shimmer 1.3s infinite;
            animation: shimmer 1.3s infinite;
    content: '';
}


.results_page-content-loader-item-text-title{
    width: 100%;
    color: rgb(223, 223, 223);
    color: var(--loader-bg);
    background-color: currentColor;
    text-decoration: none;
}

.results_page-content-loader-item-price {
    background-color: rgb(223, 223, 223);
    background-color: var(--loader-bg);
    width: 80px;
}

/* Shimmer animation for skeleton loader */
@-webkit-keyframes shimmer {
    100% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
    }
  }
@keyframes shimmer {
    100% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
    }
  }
.network_error{
    display: flex;
    flex-direction: column;
}

.network_error-icon{
    display: flex;
    justify-content: center;
}

.network_error-information{
    color: var(--font-mid);
    font-size: var(--size-normal);
}

.network_error-action-btn{
    cursor: pointer;
    color: var(--font-mid);
    text-decoration: underline;
    font-size: var(--size-normal);
    background-color: transparent;
    border: none;
}
.network_error-action-btn:hover{
    color: var(--color-primary) ;
}


