.app-header{
    position: fixed; /*because the side menu will be position:absolute*/
    height: 65px;
    width: 100%;
    display: flex;
    padding: 4px 3%;
    justify-content: space-between;
    align-items: center;
    padding-top: 5px;
    overflow: hidden;
    transition: all 0.1s linear;
}

.app-header-child{
    /* every direct child of .app-header */
    height: 60%;
}

.app-header-logo > * {
    /* Komix logo img */
    height: 100%;
}

.app-header-hamburger {
    display: flex;
    align-items: center;
    justify-content: center;
}